import { createContext } from 'react';

export const communicationContext = createContext({ send: null });

const initializeCommunication = messageHandler => {
    let target;

    const handleMessages = data => {
        console.log('handling message from host ', data);
        if (data.type === '__hello') {
            console.log('recieved target from host: ', data.domain);
            target = data.domain;
        } else {
            console.log('delegating to messageHandler');
            messageHandler(data);
        }
    };

    window.addEventListener('message', handleMessages, false);

    const send = data => {
        if (target) {
            console.log('sending message to parent: ', data);
            window.parent.postMessage(JSON.stringify({ data }), target);
        } else {
            console.log('target is not defined yet');
        }
    };

    return {
        send,
    };
};

export default initializeCommunication;
